.win {
  padding-bottom: 100px;

  &__box {
    background-image: url(../img/win-bg.svg);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    padding: 26px 20px 26px 56px;
    align-items: center;
    border-radius: 10px;
    border: 1px solid #544F61;
    position: relative;
  }

  &__img {
    position: absolute;
    bottom: 0;
    left: 50px;
    height: 100%;
    min-height: 154px;
  }

  &__title {
    margin: 0;
    margin-right: 30px;
    margin-left: 194px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 52px;
  }

  &__descr {
    margin: 0;
    margin-right: 40px;
    font-weight: 600;
    font-size: 16px;
    max-width: 180px;
  }

  &__btn {
    margin-left: auto;
    flex-shrink: 0;
  }
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(149.3deg, #F35DED 65.14%, #F3B75D 89.45%);
  box-shadow: inset 0px 4px 33px rgba(255, 255, 255, 0.37);
  border-radius: 15px;
  font-family: var(--second-family);
  font-size: 16px;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 24px 44px;

  transition: color .3s ease, border-radius .3s ease;

    &:hover {
      background: linear-gradient(149.3deg, #F3B75D 67.79%, #F35DED 89.45%);
      color: #2A2634;
      border-radius: 20px;
      transition: color .2s ease, border-radius .7s ease;
    }
}
